import noData from '@/components/noData'
import back from '@/components/back'
import goodsNav from '@/page/prefRecommend/goodsNav'
import goodsArea from '@/page/prefRecommend/goodsArea'
import { introAdmin } from '../../config'
export default {
	components: {
		noData,
		back,
		goodsNav,
		goodsArea
	},
	data() {
		return {
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: true,
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			ListDetails: this.$store.state.preList,
			activeTab: 0,
			list: [],
			list2: [],
			pageShow: false,
			istHos: false,
			num: null,
			fromIM: this.$store.state.recomMsg.fromIM,
			showConfirm: false,
			thisId: null,
			navNum: this.$store.state.navNum,
			scrollTopDistance: 0,
			tZIndex: false,
			MStyle: false,
			showCreate: false,
			introAdmin: introAdmin
		}
	},
	created() {
		this.pull()
		this.istHosGroupMember()
	},
	mounted() {
		this.getMore()
	},
	beforeDestroy() {
		window.onscroll = null
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前
		next(vm => {
			vm.getMore()
			vm.navNum = vm.$store.state.navNum
			let flag = false
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh) {
				flag = true
			}
			if(flag) {
				vm.page = 1
				vm.haveMore = true
				vm.pull()
			} else {
				setTimeout(() => {
					document.documentElement.scrollTop = to.meta.scollTopPosition
				}, 1)
			}

		});
	},
	methods: {
		istHosGroupMember() {
			this.$get('PlatFormAPI/SpecialistHosGroup/IsSpecialistHosGroupMember', this.$pick({
				DrId: this.user.Data.User.DoctorId,
			})).then(o => {
				if(o) {
					this.istHos = true
				}
			})
		},
		pull(h, n) {
			if(n) {
				this.page = 1
				this.haveMore = true
			}
			if(this.haveMore) {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendUsed', this.$pick({
					DrId: this.user.Data.User.DoctorId,
					SearchContent: this.$store.state.searchText,
					PageIndex: this.page,
					PageSize: 10
				})).then(o => {
					this.pageShow = true
					if(!o.ReturnData || o.ReturnData.length < 10) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}
					/*let _this = this
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach((v, k) => {
						let isCreatBag = false
						v.ListDetails.some(function(i) {
							if(i.GoodsSource == 3 || (i.GoodsSource == 1 && i.DrId == _this.user.Data.User.DoctorId) || (i.GoodsSource == 4 && i.DrId == _this.user.Data.User.DoctorId)) {
								isCreatBag = true
								return true;
							}
						})
						v = {
							...v,
							isCreatBag: isCreatBag
						}
						_this.list.push(v)
					})*/
					if(h) {
						o.ReturnData.forEach(v => {
							this.list.push(v)
						})
					} else {
						this.list = o.ReturnData
					}
				})
			}
		},
		creatBag(a) {
			//先清空缓存
			this.$store.dispatch('pushpreList',[])
			this.$store.dispatch('pushservice',null)
			this.$store.dispatch('pushserviceCont',null)
			//生成服务包
			let details = a.ListDetails
			let arr = [] //商品的列表
			let arr2 = [] //医生的平台服务
			let arr3 = [] //医生的自定义服务
			let arr4 = [] //查询商品的推荐信息
			let arr5 = [] //处理商品删除的情况
			let isCreatBag = false
			details.forEach((v, k) => {
				//商品类型(1-平台服务，2-专科商品，3-平台商品，4-医生自定义，5-VIP预约)
				if((v.GoodsSource == 1 || v.GoodsSource == 4) && (v.DrId != this.user.Data.User.DoctorId)) {
					isCreatBag = true
				}
				//商品
				if(v.GoodsSource == 3) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.Price = v.OnePrice
					v.VipPrice = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue = v.GoodsNum
					v.isChecked = 1
					arr.push(v)

					//查商品的数组
					let obj = {
						GoodsSource: v.GoodsSource,
						GoodsId: v.GoodsId,
						DrId: v.DrId
					}
					arr4.push(obj)
				}
				//医生的平台服务
				if(v.GoodsSource == 1 && v.DrId == this.user.Data.User.DoctorId) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.SericeItemCode = v.GoodsNo
					v.EachPrice = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue2 = v.GoodsNum
					v.isChecked = 1
					arr2.push(v)
				}
				//医生的自定义服务
				if(v.GoodsSource == 4 && v.DrId == this.user.Data.User.DoctorId) {
					v.ServiceItemId = v.GoodsId
					v.ServiceItemName = v.GoodsName
					v.SericeItemCode = v.GoodsNo
					v.Price = v.OnePrice
					v.MasterPicture = v.ImgPath
					v.NumValue = v.GoodsNum
					v.isChecked = 1
					v.Description = ""
					v.NumTypeName = "次"
					arr3.push(v)
				}
			})
			//this.$store.dispatch('pushpreList', arr)
			this.$store.dispatch('pushservice', {
				Details: arr2
			})
			this.$store.dispatch('pushcustomSevice', arr3)
			this.$store.dispatch('pushserviceBase', null)
			if(arr4 && arr4.length) {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetRecommondAmount', this.$pick({
					DrId: this.user.Data.User.DoctorId,
					Goods: arr4
				})).then(o => {
					if(o) {
						if(o.length){
							arr.forEach((v, k) => {
								o.some(function(i) {
									if(v.ServiceItemId == i.Id) {
										v.RecommondAmount = i.RecommondAmount
										v.DrIncome = i.DrIncome
										v.DrOrgOrGroupIncome = i.DrOrgOrGroupIncome
										v.RecomIncome = i.RecomIncome
										v.RecomOrgIncome = i.RecomOrgIncome
										arr5.push(v)
										return true;
									}
								})
							})
							this.$store.dispatch('pushpreList', arr5)
						}else{
							this.$store.dispatch('pushpreList', [])
						}

						if(isCreatBag){
							this.showCreate = true
						}else{
							this.$router.push("/create-service-base?fromPref=1")
						}
					}else{
						this.$router.push("/create-service-base?fromPref=1")
					}
				})
			}else{
				if(isCreatBag){
					this.showCreate = true
				}else{
					this.$router.push("/create-service-base?fromPref=1")
				}
			}
		},
		confirmCtreat() {
			//生成服务包
			this.$router.push("/create-service-base?fromPref=1")
		},
		cancleCtreat() {
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushservice', null)
			this.$store.dispatch('pushcustomSevice', [])
			this.$store.dispatch('pushserviceBase', null)
			this.$store.dispatch('pushDisease', [])
			this.$store.dispatch('pushserviceCont', null)
			this.$store.dispatch('pushserviceQuestionnaire', null)
			this.$store.dispatch('pushserviceFollowUp', null)
			this.showCreate = false
		},
		preDetail(b) {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 2, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: b.GoodsRecomId, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$router.push(`/prefRecommend/reDetail`)
		},
		preDetailC(a) {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 1, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: a.GoodsRecomUsedId, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$router.push(`/prefRecommend/reDetail`)
		},
		allGoods() {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 3, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: 0, //数据来源Id
				fromIM: this.fromIM
			})
			if(this.istHos) {
				this.$store.dispatch('pushpreList', [])
				this.$store.dispatch('pushgoodList', null)
				this.$store.dispatch('pushpreId', 0)
				this.$store.dispatch('pushscrollPo', null)
				this.$router.push(`/prefRecommend/preSpecialty`)
			} else {
				this.$store.dispatch('pushpreList', [])
				this.$store.dispatch('pusharea', null)
				this.$store.dispatch('pushallSpa', null)
				this.$store.dispatch('pushgoodList', null)
				this.$store.dispatch('pushpreId', 0)
				this.$router.push(`/prefRecommend/preAllSpecialty`)
			}
		},
		newJian() {
			this.$store.dispatch('pushrecomMsg', {
				DataSource: 1, //数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: 0, //数据来源Id
				fromIM: this.fromIM
			})
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId', 0)
			this.$store.dispatch('pushscrollPo', null)
			if(this.istHos) {
				this.$router.push(`/prefRecommend/preSpecialty`)
			} else {
				this.$router.push(`/prefRecommend/preAllSpecialty`)
			}
		},
		del(i, idx) {
			this.showConfirm = true
			this.thisId = i.GoodsRecomUsedId
			this.shHide()
		},
		shHide() {
			if(this.showConfirm) {
				this.MStyle = false
			} else {
				this.MStyle = true
			}
		},
		confirm() {
			this.$post('PlatFormAPI/SpecialistHosGroup/DelGoodsRecommendUsed', {
				id: this.thisId
			}).then(o => {
				this.$toast('已删除')
				this.page = 1
				this.haveMore = true
				this.pageShow = false
				this.pull()
			})
		},

		getMore() {
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop,
						h = el.scrollHeight,
						wh = window.innerHeight
					this.scrollTopDistance = top;
					if(top > 40) {
						this.tZIndex = true
					} else {
						this.tZIndex = false
					}
					if(h - top - wh < 10 && this.haveMore) {
						//                         this.page += 1
						// 						if(this.num==1){
						// 							this.pull2(true)
						// 						}else{
						// 							this.pull(true)
						// 						}
					}
				}, 100)
			}
		},
		goIntro() {
			//通知APP打开服务介绍
			let _url = this.introAdmin + "index.html#/prefIntro?fromAPP=1"
			let obj = {
				title: "服务介绍",
				linkUrl: _url
			}
			this.$bridge.callhandler('ServiceIntroduction', JSON.stringify(obj))
		}
	}
}



